export default class ProductsTable extends React.Component {


    constructor(){
        super()
    }

    componentWillMount(){
        this.setState(
            {
                "products": this.props.products
            }
        )
    }

    removeProduct(){

        let data = this.state.products;
        data.filter( function( a ){
            return a == 1
        });


    }

    render(){
        return (
            <div>
                <table className="table">
                    <tbody>
                    {
                        this.state.products.map( (product, i) => {
                            return (
                                <tr key={product.id}>
                                    <td>{product.name}</td>
                                </tr>
                            );
                        })
                    }
                 </tbody>
                </table>
            </div>
        );
    }

}